$accent-color: #008cef;
$accent-color-hover: #0d73bd;

$main-text-color: #2b2b2b;
$secondary-text-color: #cdcdcd;

$main-background-color: #ffffff;

$main-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
$main-duration: 250ms;

$btn-shadow: -10px -10px 20px #ffffff, 10px 10px 20px rgba(72, 121, 150, 0.2),
  5px 5px 10px rgba(72, 121, 150, 0.25), -5px -5px 10px #ffffff;
$btn-shadow-hover: -10px -10px 20px #ffffff,
  10px 10px 20px rgba(72, 121, 150, 0.5), 5px 5px 10px rgba(72, 121, 150, 0.25),
  -5px -5px 10px #ffffff;

$hamburger-padding-x: 0;
$hamburger-padding-y: 0;
$hamburger-layer-width: 32px;
$hamburger-layer-height: 2px;
$hamburger-layer-spacing: 6px;
$hamburger-layer-color: $accent-color;
